<template>
    <article class="bgWrapper" id="works">
        <div class="contentWrapper">

        <h2 class="prmryHeading">WORKS</h2>
        <div class="mt-10 md:flex justify-around flex-wrap">
            
            <article v-for="item in work_arr" :key="item.name"
                class="w-full md:w-72 px-6 py-8 mb-8 bg-gradient-to-br from-stone-50 to-stone-100 shadow-md md:mr-auto border border-slate-100"
            >
                <img class="w-[256px] h-[144px] object-cover mx-auto rounded-sm border border-slate-400" :src="item.img" :alt="item.name">
                <h3 class="workTitle mt-8">{{ item.name }}</h3>
                <div class="flex mt-2 w-full h-fit">
                    <img v-show="item.avail.pc" src="../assets/pc.svg" class="w-5 h-5 mt-1">
                    <img v-show="item.avail.sm" src="../assets/sma_phone.svg" class="w-5 h-5 mt-1 ml-1">
                    <div class="ml-1 w-full">
                        <a :href="item.URL" target="_blank" rel="noopener noreferrer" :class="[(item.avail.sm == false && isMobile) ? 'respDisableLink': 'text-cyan-600 underline'] + ' block w-4/5 md:w-52 whitespace-nowrap overflow-hidden text-ellipsis'"> {{ item.URL }} </a>
                        <p :class="[!(item.avail.sm == false && isMobile) && 'hidden'] + ' text-slate-400 text-xs ml-1' ">モバイル未対応です</p>
                    </div>
                </div>

                <button @click="(ev) => handleInfoBtn(item, ev)" class="block mt-6 ml-auto text-sm text-emerald-600 underline">>詳細を開く</button>
            </article>

        </div>

        <article v-show="infoItem.name != ''" class="__infoElm fixed z-40 w-screen h-screen overflow-y-scroll overscroll-contain top-0 left-0">
            <div class="fixed -z-10 top-0 left-0 w-full h-full bg-slate-950 bg-opacity-80"></div>
            <section @click="(ev) => ev.stopPropagation()" class="w-5/6 max-w-3xl mx-auto px-4 md:px-40 py-28 h-200vh bg-slate-700 bg-opacity-80 backdrop-blur">
                <img class="w-[256px] h-[144px] md:w-[512px] md:h-[288px] object-cover mx-auto rounded-sm border-2 border-slate-500" :src="infoItem.img" :alt="infoItem.name">
                <section class="text-slate-50 mt-6 md:mt-10">

                    <h3 class="text-sm mb-1">作品名</h3>
                    <p class="scdryHeading ml-2"> {{ infoItem.name }} </p>

                    <section class="mt-6">
                        <h4 class="text-sm mb-1">開発言語、フレームワーク等</h4>
                        <ul class="list-none">
                            <li v-for="tool in infoItem.tool" :key="tool" class="font-semibold ml-2">{{ tool }}</li>
                        </ul>

                    </section>

                    <section class="mt-6">
                        <h4>対応機種 : 
                            {{ infoItem.avail.pc ? "PC " : "" }} {{ infoItem.avail.sm ? "スマートフォン" : "" }}
                        </h4>
                        <p class="mt-1">URL : <a :href="infoItem.URL" target="_blank" rel="noopener noreferrer" :class="[(infoItem.avail.sm == false && isMobile) ? 'respDisableLink': 'text-cyan-500 underline']">{{ infoItem.URL }} </a></p>
                    </section>

                    <section class="mt-6">
                        <h4 class="text-sm mb-2">コメント</h4>
                        <p v-for="(text, idx) in infoItem.comment" :key="idx" class="text-sm md:text-base mb-1 ml-1"> {{ text }} </p>
                    </section>
                    <button @click="(ev) => handleCloseBtn(ev)" class="block mt-12 ml-auto mr-2 text-sm text-slate-300 underline px-2 bg-slate-600 rounded">>閉じる</button>

                </section>
            </section>
        </article>
            
        </div>

    </article>

</template>

<script setup>

    import { ref } from 'vue';

    const infoItem = ref(
        {
            name: "",
            img: "",
            avail: { pc: false, sm: false },
            URL: "",
            tool: [],
            comment: [""],
        },
    );


    const isMobile = (window.ontouchstart !== undefined) && (0 < navigator.maxTouchPoints);

    const work_arr = [
        {
            name: "ペグボードマップ",
            img: require("../../public/pegboardMap.webp"),
            avail: { pc: true, sm: true },
            URL: "https://sg1t.github.io/pegboardMap/",
            tool: ["React", "Tailwind CSS", "TypeScript"],
            comment: [
                "有孔板を模した地図メモアプリです。グリッド別に地名やアイコンを持つピンを設置できます。",
                "手近の地図アプリは多機能な一方で「馴染みのない経路がナビ候補に挙がる」「誤操作で読み込み待ちが発生する」といった弊害もあり、ごく近所の地理をメモする程度のものが欲しいと思い制作にあたりました。",
                "ピン同士の連結関係は幅優先探索によって求めており、「自宅」ピンからの連結関係をたどっても到着できないピンは薄く表示されます。",
            ],
        },
        {
            name: "パターンエディタ",
            img: require("../../public/tile_ss.webp"),
            avail: { pc: true, sm: false },
            URL: "https://sg1t.github.io/tileEditor/",
            tool: ["React", "Tailwind CSS", "Canvas API"],
            comment: [
                "繰り返し模様の原版となる画像を制作するWebアプリです。制作した画像は任意の形式で保存でき、CSSのリピート背景等に利用できます。",
                "ペイントソフトが標準で備えるツールのほか、描画した部分を線対称に反映する機能も実装しました。これにより、端同士の継ぎ目のない模様を描くことができます。",
                "各種ツール・機能の詳細は、画面右上のヘルプ機能を有効にしたうえで表示されるメッセージを参照してください。",
            ],
        },
        {
            name: "タイマーアプリ",
            img: require("../../public/timerApp.webp"),
            avail: { pc: true, sm: true },
            URL: "https://sg1t.github.io/timerApp/",
            tool: ["React", "axios", "Unsplash API"],
            comment: [
                "インスタントカメラのフィルム現像を模したタイマーアプリです。タイマーの進行に応じて無地の写真が鮮明になっていきます。",
                "シンプルなタイマー機能をベースに、進行を表すゲージやアラーム機能、スタンプ付きのカレンダーなど、アプリに関連しつつ実装できそうな機能を盛り込みました。",
                "写真素材サイト「Unsplash」の提供するAPIから画像URLを取得しており、カウントを開始するたびにランダムな写真が読み込まれます。",
            ],
        },
        {
            name: "単語帳アプリ",
            img: require("../../public/fca_ss.webp"),
            avail: { pc: true, sm: true },
            URL: "https://sg1t.github.io/fca/",
            tool: ["Vue.js", "Vuex", "Firebase"],
            comment: [
                "職業訓練の実習にて制作した単語帳アプリです。ログイン機能により異なる端末からでも同じデータを利用できます。",
                "バックエンド関連の知識は皆無でしたが、公式のリファレンスやノウハウ共有サービスの投稿等を参考に開発を進め、ログイン情報をVuex(状態管理ライブラリ)のストアに保持する形で実装しました。",
                "新規登録にあたってメールアドレスの入力を求められますが、@以降のドメインが公式のものかつパスワードが6文字以上であれば「(今日の日付) + @gmail.com」のような架空のアドレスでも登録できます。",
            ],
        },
        
    ]
    function handleCloseBtn(ev) {
        ev.stopPropagation();
        infoItem.value = {
            name: "",
            img: "",
            avail: { pc: false, sm: false },
            URL: "",
            tool: [],
            comment: [""],
        }
    }

    function handleInfoBtn(obj, ev) {
        ev.stopPropagation();

        const setInfo = async function () {
            infoItem.value = obj;
        }

        setInfo().then(() => {
            
            let infoElm = document.getElementsByClassName("__infoElm")[0];
            infoElm.scrollTo({
                top: 0,
                left: 0,
                behavior: "instant",
            });
        })

    }


    window.addEventListener("click", handleCloseBtn);


</script>

<style>
</style>
