<template>
    <header class="fixed top-0 z-40 w-full h-fit px-6 py-2 md:py-4 bg-gradient-to-b bg-slate-600">
        <div class="w-full max-w-5xl mx-auto flex justify-around md:justify-between">
            <h2 class="relative w-fit h-7 my-auto transition duration-1000" :class="[refView=='top' ? '-z-10 opacity-0 translate-y-0.5' : 'z-0 opacity-100 translate-y-0' ]">
                <a href="#top" class="inline-block text-xl font-bold text-center text-slate-200">
                    Portfolio Site
                </a>
            </h2>

            <!-- for PC menu -->
            <menu class="hidden md:block list-none h-7">
                <li class="inline-block h-full mr-2">
                    <a href="#top" class="inline-block w-16 font-semibold text-lg text-center transition duration-500" :class="[refView=='top' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                    Top
                    </a>
                </li>
                <li class="inline-block h-full mr-2">
                <a href="#profile" class="inline-block w-16 font-semibold text-lg text-center transition duration-500" :class="[refView=='profile' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                    profile
                    </a>
                </li>
                <li class="inline-block h-full mr-2">
                <a href="#works" class="inline-block w-16 font-semibold text-lg text-center transition duration-500" :class="[refView=='works' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                    Works
                    </a>
                </li>
            </menu>

            <!-- for Mobile menu -->
            <button class="relative flex flex-col justify-around ml-auto mr-0 p-1 md:hidden md:pointer-events-none  w-10 h-10" @click="handleMenuBtn" aria-label="メニューボタン">
                <span class="block w-full h-1 bg-slate-200 rounded transition-[transform] duration-500 origin-left" :class="{ 'rotate-45':isMenuRef }"></span>
                <span class="block h-1 bg-slate-200 rounded transition-[width] duration-500 mx-auto" :class="isMenuRef ? 'w-0' : 'w-full'"></span>
                <span class="block w-full h-1 bg-slate-200 rounded transition-[transform] duration-500 origin-left" :class="{ '-rotate-45':isMenuRef }"></span>
            </button>

            

        </div>
    </header>

    <!-- Mobile menu slide -->
    <div class="fixed top-0 left-0 z-[39] w-screen h-screen bg-slate-700 bg-opacity-90 transition duration-500 md:hidden" :class="isMenuRef?'translate-x-0':'translate-x-full'">
        <menu class="list-none w-20 mt-20 ml-auto mr-6">
                <li class="mb-3">
                    <a href="#top" class="inline-block w-20 font-semibold text-lg text-center underline transition duration-500" :class="[refView=='top' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                    Top
                    </a>
                </li>
                <li class="mb-3">
                <a href="#profile" class="inline-block w-20 font-semibold text-lg text-center underline transition duration-500" :class="[refView=='profile' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                    profile
                    </a>
                </li>
                <li class="mb-3">
                <a href="#works" class="inline-block w-20 font-semibold text-lg text-center underline transition duration-500" :class="[refView=='works' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                    Works
                    </a>
                </li>
            </menu>
    </div>
</template>

<script setup>

    import { ref, onMounted } from 'vue';

    const refView = ref("top");

    const isMenuRef = ref(false);

    let headScroll_flag = true;

    let profile_elm;
    let works_elm;

    function handleMenuBtn() {
        isMenuRef.value = !isMenuRef.value;
    }


    onMounted(() => {
        profile_elm = document.getElementById("profile");
        works_elm = document.getElementById("works");
    });
    
    window.addEventListener("scroll", () => {
        if(headScroll_flag == false) {
            return;
        }

        headScroll_flag = false;
        let timeOut = setTimeout(() => {
            headScroll_flag = true;
            clearTimeout(timeOut);
        }, 200);

        // let top_y = top_elm.getBoundingClientRect().top;
        let profile_y = profile_elm.getBoundingClientRect().top;
        let works_y = works_elm.getBoundingClientRect().top;

        refView.value = "top"

        if(profile_y <= 200) {
            refView.value = "profile";
        }
        if(works_y <= 200) {
            refView.value = "works";
        }
    })

    

</script>